<template>
    <div class="box">
        <div class="choiceBox">
            <el-select v-model="workingYears" placeholder="工作年数" class="choice">
                <el-option v-for="item in workYearsC" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
            <el-select v-model="education" placeholder="教育背景" class="choice">
                <el-option v-for="item in eduBackgroundC" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
            <el-select v-model="age" placeholder="年龄" class="choice">
                <el-option v-for="item in ageC" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
            <el-button type="primary" @click="search">查询</el-button>
            <el-button @click="resetForm">重置</el-button>
        </div>
        <div class="resume" :style="{ display: hidden }" v-for="i in upLoadList" :key="i.id">
            <div class="name">{{ i.name }}</div>
            <div class="age"><span>年龄：{{ i.age }}</span> <span>工作年限：{{ i.workingYears }}</span> <span>学历：{{ i.education
            }}</span></div>
            <div class="want"><span>求职期望：{{ i.career }} </span></div>
            <div class="honor"><span>荣誉证书：{{ i.honor }}</span></div>
            <div class="ex">主要经历：{{ i.workExperience }}</div>
            <el-rate v-model="score" disabled text-color="#ff9900" class="score">
            </el-rate>
            <el-button type="primary" class="details" size="mini" @click="goDetail(i.id)">查看详情</el-button>
            <el-button type="info" class="cancel" size="mini" @click="hide(i.id)">删除此项</el-button>
        </div>
    </div>
</template>

<script>
// import { reqUserResume } from '@/api';
import { reqUserUpload, reqDelResume, reqSearch } from '@/api'
export default {
    name: 'upload',
    data() {
        return {
            hidden: 'block'
            ,
            formInline: {
                user: "",
                region: ""
            },
            workYearsC: [{
                value: '1',
                label: '0'
            }, {
                value: '2',
                label: '0—3年'
            }, {
                value: '3',
                label: '3—6年'
            }, {
                value: '4',
                label: '6-9年'
            }, {
                value: '5',
                label: '9-16年'
            }, {
                value: '6',
                label: '16及以上年'
            }],
            workingYears: '',
            eduBackgroundC: [{
                value: '2',
                label: '大专'
            }, {
                value: '3',
                label: '本科'
            }, {
                value: '4',
                label: '硕士'
            }, {
                value: '1',
                label: '大专以下'
            }, {
                value: '5',
                label: '博士及以上'
            }],
            education: '',
            ageC: [{
                value: '1 ',
                label: '18-23 '
            }, {
                value: '2 ',
                label: '23-28 ',
            }, {
                value: '  3 ',
                label: '28-33  ',
            },
            {
                value: '  4',
                label: '33-38   ',
            },
            {
                value: ' 5 ',
                label: '38-43',
            }, {
                value: '6',
                label: '43-48'
            }, {
                value: '7',
                label: '48+'
            }],
            age: '',
            area: '',
            score: '4',
            id: '1661341670245003266',
            upLoadList: []
        };
    },
    methods: {
        onSubmit() {
            console.log("submit!");
        },
        goDetail(Id) {
            this.$router.push({ name: 'Analyze', params: { id: Id } })
        },
        async hide(id) {
            const result = confirm('确定删除吗');
            if (result) {
                const res = await reqDelResume(id)
                if (res.code == 1) {
                    this.$message({
                        message: '删除成功',
                        type: 'success'
                    });
                    this.getUserUpload()
                }
            } else {
                // 用户点击了取消按钮
                // 执行其他操作或不执行任何操作
            }

        },
        async getUserUpload() {
            const res = await reqUserUpload(localStorage.getItem('id'))
            if (res.code == 1) {
                this.upLoadList = res.data
            }
        },
        resetForm() {
            this.workingYears = '',
                this.education = '',
                this.age = '',
                this.getUserUpload()
        },
       async search() {
            const id = localStorage.getItem('id')
            const { workingYears, education, age } = this
            const data = { workingYears, education, age }
            const res = await reqSearch(id,data)
            if (res.code == 1) {
                console.log(15);
                this.upLoadList=res.data
            }
        }
    },
    mounted() {
        this.getUserUpload()
    }
}
</script>
<style lang="scss" scoped>
::v-deep .el-input__inner {
    border-color: black;
    color: black;

    &::placeholder {
        color: grey;
    }
}

.box {
    width: 83%;

    float: left;
    margin-left: 20px;
    margin-top: 50px;

    // background-color:blue ;
    .choiceBox {
        width: 60%;
        height: 50px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        transform: translate(-6px, -20px);

        .choice {
            scale: 0.9;
        }

        // .choice {
        //     border: 1px black solid;
        //     border-radius: 5%;
        // }
    }

    .resume {
        width: 100%;
        border: 1px solid black;
        height: 150px;
        margin-top: -20px;
        margin-bottom: 15px;
        position: relative;

        .name {
            position: absolute;
            top: 15%;
            left: 8%;
            font-weight: 700;
            font-size: 15px;
        }

        .age {
            position: absolute;
            top: 35%;
            left: 90px;
            width: 250px;

            span {
                margin-left: 10px;
            }
        }

        .want {
            position: absolute;
            top: 55%;
            width: 250px;
            left: 90px;

            span {
                margin-left: 10px;
            }
        }

        .honor {
            position: absolute;
            top: 75%;
            width: 250px;
            left: 90px;


            span {
                margin-left: 10px;
                height: 30px;
                display: block;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        .ex {
            width: 40%;
            height: 55%;
            top: 30%;
            left: 40%;
            position: absolute;
            background-color: rgb(246, 249, 252);
            overflow-y: scroll;
        }

        .score {
            position: absolute;
            left: 15%;
            top: 15%;
        }

        .details {
            position: absolute;
            right: 5%;
            top: 30%;
        }

        .cancel {
            position: absolute;
            right: 5%;
            top: 65%;
        }
    }

}
</style>